* {
  font-family: 'Inter', sans-serif;
}
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  background: white;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #888;
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #555;
}
::-webkit-scrollbar-track {
  border-radius: 40px;
}
.a-main {
  justify-content: center;
  align-items: center;
  width: 100%;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: row;
  height: 100vh;
  overflow-y: hidden;
}
.left-panel {
  width: 280px;
  height: 100vh;
  /* overflow-y: scroll; */
  flex-wrap: wrap;
  border-right: 2px solid rgb(95, 94, 94);
  background-color: rgb(20, 13, 13);
  color: white;
}
.right-panel {
  height: 100vh;
  overflow-y: scroll;
  width: calc(100% - 280px);
}

.left-panel > .heading {
  font-weight: 700;
  font-size: 30px;
  padding: 30px;
}
.overview {
  margin-bottom: 20px;
}
.overview > p:first-child,
.my-events > div:first-child {
  font-weight: 600;
  margin: 0;
  padding: 5px 25px;
}
.overview-fields {
  font-size: 14px;
  display: flex;
  flex-direction: row;
  padding: 5px 30px;
  border-left: 2px solid rgb(20, 13, 13);
  cursor: pointer;
}
.overview-fields:hover {
  background-color: rgba(255, 255, 255, 0.151);
  border-left: 2px solid white;
}
.overview-fields > p,
.events-fields > p {
  margin: 0;
  padding: 5px 0;
}
.overview-fields > .icn {
  font-size: 22px;
  color: white;
  margin-right: 4px;
}

.my-events > div {
  display: flex;
  flex-direction: row;
}
.my-events > div > .btn {
  background-color: rgb(34, 38, 39);
  color: white;
  outline: none;
  font-size: 12px;
  border-radius: 4px;
  border: 2px solid white;
  cursor: pointer;
  border-radius: 5px;
  padding: 5px 15px;
  margin-left: auto;
}
.internship > div > .btn {
  background-color: rgb(34, 38, 39);
  color: white;
  outline: none;
  font-size: 10px;
  border-radius: 4px;
  border: 2px solid white;
  cursor: pointer;
  border-radius: 5px;
  padding: 6px 4px;
  margin-left: auto;
}
.topbar {
  width: 100%;
  position: sticky;
  background-color: white;
  top: 0;
  z-index: 15;
  border-bottom: 2px solid #888;
}
.topbar > p {
  font-size: 17px;
  font-weight: 700;
  margin-left: 50px;
  margin: 30px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 20px 0;
  width: max-content;
}
.website-tree {
  display: flex;
  flex-direction: row;
  margin: 4px 0;
}
.website-tree > p {
  margin: 0 3px;
  cursor: pointer;
  font-size: 14px;
  font-weight: 700;
  color: rgb(83, 83, 138);
}
.website-tree > .parent {
  color: rgb(6, 6, 53);
}
.website-tree > p:hover {
  color: rgb(47, 47, 185);
}
.companies-list > .headerr {
  display: flex;
  flex-direction: row;
}
.companies-list > .headerr > p:first-child {
  font-weight: 600;
  font-size: 14px;
  margin: 5px;
  margin-left: 30px;
}
.headerr {
  margin: 7px 0;
}
.add-btn {
  font-size: 13px;
  cursor: pointer;
  padding: 6px 6px;
  background-color: #555;
  color: white;
  border-radius: 3px;
  margin: 0;
  margin-right: 5px;
}
.list {
  display: flex;
  flex-direction: row;
  /* flex-flow: row wrap; */
  margin: 0 30px;
}

.cols {
  width: 22%;
  padding: 0 10px;
  /* flex-flow: row wrap; */
  /* flex-basis: 30%; */
  /* text-align: center; */
}
.cols > p {
  margin: 10px 0;
  color: rgb(29, 70, 184);
  cursor: pointer;
}
.cols > p:hover {
  text-decoration: underline;
}
@media (max-width: 1000px) {
  .list {
    flex-direction: column;
  }
  .cols {
    width: calc(100% - 20px);
  }
}
.rel {
  position: relative;
}
.addcompany {
  width: 80%;
  margin: 0 10%;
}
.addcompany > input {
  width: calc(100% - 22px);
}
.addcompany > #ex {
  width: 100%;
}
.addcompany > .upload {
  width: 50%;
}
.companypage > .btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 20px;
}
.companypage > .btn > button {
  padding: 8px 12px;
  text-align: center;
  color: white;
  font-size: 16px;
  background-color: black;
  border-radius: 5px;
  margin-bottom: 10px;
  box-shadow: none;
}
.tag-list {
  margin-left: auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  /* width: 300px; */
}
.tag-con {
  display: flex;
  flex-direction: row;
  margin: 3px;
  border-radius: 30px;
  background-color: #555;
}
.tag-con > p:last-child {
  /* margin: 0; */
  margin: 1px 5px;
  padding: 3px 7px;
  margin-left: 0px;
  padding-left: 0;
  color: white;
  cursor: pointer;
}
.tag-con > div {
  height: 28px;
}
.close-x {
  padding: 4px 7px;
  font-size: 20px;
  padding-left: 0px;
  cursor: pointer;

  color: rgba(224, 219, 219, 0.486);
}
.close-x > path {
  pointer-events: none;
}
.close-x:hover {
  color: rgba(224, 219, 219, 0.781);
}
.tag {
  color: white;
  font-size: 14px;
  font-weight: 500;
  margin: 0px 5px;
  padding: 4px 7px;
  padding-top: 4.5px;
}


.preference__modal__roles__role, .preference__modal__roles__role__selected {
  -webkit-tap-highlight-color: transparent;
  display: inline-flex;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: .35rem .25rem;
  padding: .7rem .875rem;
  font-size: .875rem;
  text-align: center;
  font-weight: 400;
  line-height: 1.25;
}

.preference__modal__roles__role {
  color: #788699;
  border: 1px solid #d6dae0;
  border-radius: 1.5rem;
  cursor: pointer;
}

.preference__modal__roles__role__selected {
  color: #fff;
  border: 1px solid #61a0ff;
  background: #61a0ff;
  border-radius: 1.5rem;
  cursor: pointer!important;
}

.locInput{
  outline: none;
}

ul.addloc{
  padding: 0;
  margin-top: 5px;
  margin-left: 1px;
  margin-right: 1px;
  list-style-type: none;
  text-align: left;
  border-radius: 2px;
}

ul.addloc::before{
  content: "";
}

ul.addloc>li{
  padding: 9px;
  cursor: pointer;
  /* font-size: 13px; */
  font-weight: bold;
}

ul.addloc>li:hover{
  /* text-decoration: underline; */
  background-color: rgba(128,128,128,0.20);
}

.addloc{
  box-shadow: 0 0 0 1px rgba(0,0,0,.1) , 0 2px 4px 1px rgba(0,0,0,.18);
  
}