.input-row {
    margin-top: 30px;
    padding: 40px;
}

.display-tags{
    padding-top: 40px;
}

.preference__modal__roles__role, .preference__modal__roles__role__selected {
    -webkit-tap-highlight-color: transparent;
    display: inline-flex;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    margin: .35rem .25rem;
    padding: .7rem .875rem;
    font-size: .875rem;
    text-align: center;
    font-weight: 400;
    line-height: 1.25;
}

.preference__modal__roles__role {
    color: #788699;
    border: 1px solid #d6dae0;
    border-radius: 1.5rem;
    cursor: pointer;
}

.preference__modal__roles__role__selected {
    color: #fff;
    border: 1px solid #61a0ff;
    background: #61a0ff;
    border-radius: 1.5rem;
    cursor: pointer!important;
}

form {
    display: block;
    margin-top: 0em;
}

.form-inline {
    /* display: -ms-flexbox; */
    display: flex;
    /* -ms-flex-flow: row wrap; */
    flex-flow: row wrap;
    /* -ms-flex-align: center; */
    align-items: center;
}

.row {
    /* display: -ms-flexbox; */
    display: flex;
    -ms-flex-wrap: wrap;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px;
}

.col {
    /* -ms-flex-preferred-size: 0; */
    flex-basis: 0;
    /* -ms-flex-positive: 1; */
    flex-grow: 1;
    max-width: 100%;
    padding-right: 40px;
    padding-left: 15px;
}

.form-control {
    display: block;
    width: 100%;
    /* height: calc(1.5em + .75rem + 2px); */
    padding: .375rem .75rem;
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

button, input {
    overflow: visible;
}

button, input, optgroup, select, textarea {
    margin: 0;
    font-family: inherit;
    font-size: inherit;
    line-height: inherit;
}

.btn:not(:disabled):not(.disabled) {
    cursor: pointer;
}


.btn {
    display: inline-block;
    font-weight: 400;
    color: #212529;
    text-align: center;
    vertical-align: middle;
    /* -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none; */
    user-select: none;
    background-color: transparent;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    font-size: 1rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
}

.btn-primary {
    color: #fff;
    background-color: #007bff;
    border-color: #007bff;
}
